@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;400;700&family=Roboto:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
}

#root{
    height: 100%;
    width: 100%;
}
h3{
  color: var(--green);
  font-weight: normal;
  text-align: center;
}
.container{
  max-width: 1040px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.header{
  max-width: 160px;
  width: 100%;
  margin-right: 16px;
  display: flex;
  flex-direction: column;
  align-content: space-between;
}
.titleGroup{
  width: 120px;
  height: 120px;
  margin: 60px 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  box-sizing: border-box;
  border: 4px solid var(--green);
  border-radius: 4px;
}
.title{
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  color: var(--green);
  text-align: center;
}
.todoItemGroup{
  max-width: 864px;
  width: 100%;
}
.addTodoGroup{
  height: 50px;
  display: flex;
  justify-content: space-between;
  margin-top: 108px;
  position: relative;
}
.todotype{
  max-width: 790px;
  width: 88%;
  text-decoration: none;
  padding-left: 36px;
  color: var(--white);
}
.todotype:focus, .todotype:active{
  border: 0;
}
.todoAdd{
  width: 50px;
  margin-left: 24px;
  font-size: 36px;
  font-weight: bold;
  color: var(--white);
  letter-spacing: 0;
}
.todoAdd:hover{
  background-color: var(--green);
}
.todoAdd:active{
  background-color: var(--dark-g);
}
.todotype, .todoAdd{
  background-color: var(--light-g);
  box-shadow: 1px 1px 8px rgba(53, 79, 82, 0.25);
  border-radius: 15px;
  border: 0;
}
.todoListItemGroup{
  margin-top: 48px;
  border-top: 1px solid var(--gray-g);
}
.todoListItem{
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  border-bottom: 1px solid var(--gray-g);
}
.itemValue{
  display: flex;
  align-items: center;
}
.itemValue input{
  width: 20px;
  height: 20px;
  margin-left: 24px;
}
.itemValue p{
  max-width: 728px;
  padding: 12px 0;
  margin: 0 24px;
  color: var(--dark-g);
  font-weight: 600;
  text-align: left;
}
.deleteItemBtn{
  width: 30px;
  height: 30px;
  margin: 0 12px;
  font-size: 20px;
  font-weight: bold;
  color: var(--green);
  border: 0;
  background-color: rgba(255, 255, 255, 0);
  letter-spacing: 0;
}
.deleteItemBtn:hover{
  color: var(--white);
  background-color: var(--gray-g);
  border-radius: 4px;
}

.filterBtnGroup{
  max-width: 700px;
  display: flex;
  flex-direction: column;
}
.filterBtn{
  width: 100%;
  height: 24px;
  font-weight: bold;
  margin: 12px 0;
  padding-left: 30px;
  display: flex;
  align-items: center;
  text-align: left;
  text-decoration: none;
  color: var(--light-g);
  background-color: rgba(255, 255, 255, 0);
  border: 0;
}
.filterBtn:hover{
  color: var(--deep-g);
}
.filterBtn:focus, .defaultBtn{
  padding-left: 24px;
  color: var(--deep-g);
  border-left: 6px solid var(--light-g);
  font-weight: bolder;
}

* {
    font-family:"Roboto", "Noto Sans TC", sans-serif;
    font-size: 18px;
    letter-spacing: 0.2em;
}

:root{
    --white:#fff;
    --gray-g: #cad2c5;
    --light-g: #84a98c;
    --green: #52796f;
    --dark-g: #354f52;
    --deep-g: #2f3e46;
}

::-webkit-input-placeholder {
    color: #fff;
    color: var(--white);
    font-size: 18px;
}

:-ms-input-placeholder {
    color: #fff;
    color: var(--white);
    font-size: 18px;
}

::placeholder {
    color: #fff;
    color: var(--white);
    font-size: 18px;
}

